.account{
    width: 50px;
    height: 50px;
}
.post-head{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    margin-top: 10px;
}
.post-head button{
    position: absolute;
    right: 0;
}
.post{
    background-color: rgb(255, 255, 255);
    margin-top: 10px;
    height: fit-content;
    padding: 10px;
    scroll-snap-align: start;
}
.bio{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.reaction{
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}